import Link from "next/link";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { TextField, IconButton, InputAdornment } from "@mui/material";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

"use client";


/*
  Professional, smoother signup UI:
  - Muted navy / teal color palette
  - Subtle glass card, soft shadows, smooth transitions
  - Basic client validation before network call
  - Proper isSubmitting handling and accessibility improvements
*/

const EMAIL_RE = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

const Signup = () => {
  const router = useRouter();

  const [userData, setUserData] = useState({
    username: "",
    email: "",
    password: "",
    confirmPassword: "",
    userType: "",
  });

  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    // small artificial load for smooth reveal
    const t = setTimeout(() => setLoading(false), 220);
    return () => clearTimeout(t);
  }, []);

  const changeHandler = (e) => {
    const { name, value } = e.target;
    setUserData((prev) => ({ ...prev, [name]: value }));

    if (errors[name]) {
      setErrors((prev) => ({ ...prev, [name]: null }));
    }
  };

  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((s) => !s);
  const handleClickShowConfirmPassword = () => setShowConfirmPassword((s) => !s);

  const validate = () => {
    const e = {};
    if (!userData.userType) e.userType = "Select an account type.";
    if (!userData.username || userData.username.trim().length < 2)
      e.username = "Enter a valid username (2+ characters).";
    if (!EMAIL_RE.test(userData.email)) e.email = "Enter a valid email address.";
    if (!userData.password || userData.password.length < 8)
      e.password = "Password must be 8+ characters.";
    if (userData.password !== userData.confirmPassword) e.confirmPassword = "Passwords must match.";
    setErrors(e);
    return Object.keys(e).length === 0;
  };

  const submitHandler = async (e) => {
    e.preventDefault();
    if (!validate()) {
      toast.error("Please fix validation errors", { position: "top-right", autoClose: 3000 });
      return;
    }

    setIsSubmitting(true);

    try {
      const res = await axios.post("http://localhost:5000/api/auth/register", {
        username: userData.username,
        email: userData.email,
        password: userData.password,
        confirmPassword: userData.confirmPassword,
        userType: userData.userType,
      });

      if (!res.data.error) {
        toast.success(res.data.message || "Registration successful!", {
          position: "top-right",
          autoClose: 2000,
          onClose: () => router.push("/Login"),
        });
      } else {
        toast.error(res.data.message || "Registration failed", { position: "top-right", autoClose: 4000 });
      }
    } catch (err) {
      if (err.response?.data) {
        const msg = err.response.data.message || err.response.data.error || "Server error";
        toast.error(msg, { position: "top-right", autoClose: 4000 });
      } else if (err.request) {
        toast.error("Network error: Server did not respond", { position: "top-right", autoClose: 4000 });
      } else {
        toast.error(`Error: ${err.message}`, { position: "top-right", autoClose: 4000 });
      }
    } finally {
      setIsSubmitting(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-slate-50">
        <div className="text-center">
          <div className="animate-spin h-12 w-12 border-4 border-t-teal-500 border-slate-200 rounded-full mx-auto" />
          <p className="mt-3 text-slate-600">Preparing secure signup...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex flex-col lg:flex-row bg-slate-50">
      {/* Left: marketing / info */}
      <aside
        className="w-full lg:w-1/2 flex items-center justify-center p-10"
        aria-hidden="true"
        style={{
          background:
            "linear-gradient(135deg, rgba(6,95,70,0.08) 0%, rgba(8,30,63,0.06) 100%)",
        }}
      >
        <div className="max-w-lg space-y-6 relative">
          <div className="backdrop-blur-sm bg-white/6 rounded-3xl p-6 border border-slate-100/10 shadow-lg">
            <h1 className="text-3xl font-semibold text-slate-800 mb-2">
              {userData.userType === "Recruiter"
                ? "Hire smarter — faster"
                : userData.userType === "Job Seeker"
                ? "Let your skills speak"
                : "Intelligent recruiting, simplified"}
            </h1>
            <p className="text-sm text-slate-600">
              {userData.userType === "Recruiter"
                ? "Structured insights and voice screening to surface top talent quickly."
                : userData.userType === "Job Seeker"
                ? "Showcase your strengths with voice interviews and live challenges."
                : "AI tools that reduce screening time and help match the right people."}
            </p>
            <div className="mt-4 flex flex-wrap gap-2">
              <span className="text-xs px-3 py-1 rounded-full bg-teal-50 text-teal-700">GDPR-friendly</span>
              <span className="text-xs px-3 py-1 rounded-full bg-slate-50 text-slate-700">Bias-aware</span>
              <span className="text-xs px-3 py-1 rounded-full bg-amber-50 text-amber-700">Secure</span>
            </div>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            <div className="p-4 rounded-2xl bg-white/80 shadow-sm border border-slate-100/8 transition-transform transform hover:-translate-y-1">
              <h3 className="font-semibold text-slate-800">Recruiter</h3>
              <p className="text-sm text-slate-600 mt-2">
                Calibrated scoring, concise candidate summaries, and reduced time-to-hire.
              </p>
            </div>
            <div className="p-4 rounded-2xl bg-white/80 shadow-sm border border-slate-100/8 transition-transform transform hover:-translate-y-1">
              <h3 className="font-semibold text-slate-800">Job Seeker</h3>
              <p className="text-sm text-slate-600 mt-2">
                Present real ability — voice, challenges, and a focused profile employers value.
              </p>
            </div>
          </div>

          <div className="mt-4 grid grid-cols-3 gap-3 text-xs text-slate-600">
            {["Plaid", "OpenAI", "Airtable"].map((n) => (
              <div key={n} className="py-2 rounded-lg bg-slate-50 text-center">
                {n}
              </div>
            ))}
          </div>
        </div>
      </aside>

      {/* Right: form */}
      <main className="w-full lg:w-1/2 flex items-center justify-center p-8">
        <div className="w-full max-w-md">
          <div className="bg-white rounded-3xl p-6 sm:p-8 shadow-xl border border-slate-100 transition-all">
            <h2 className="text-2xl sm:text-3xl font-extrabold text-slate-800 text-center mb-4">
              Create Account
            </h2>

            <form onSubmit={submitHandler} className="space-y-4">
              <div className="flex gap-3 mb-1" role="tablist" aria-label="Account type">
                {["Job Seeker", "Recruiter"].map((type) => {
                  const active = userData.userType === type;
                  return (
                    <button
                      key={type}
                      type="button"
                      onClick={() => changeHandler({ target: { name: "userType", value: type } })}
                      aria-pressed={active}
                      className={`flex-1 py-2 rounded-xl text-sm font-medium transition-all focus:outline-none focus:ring-2 focus:ring-offset-1 ${
                        active
                          ? "bg-gradient-to-r from-slate-800 to-teal-600 text-white shadow-md ring-1 ring-slate-900/10"
                          : "bg-slate-50 text-slate-700 border border-slate-100"
                      }`}
                    >
                      {type}
                    </button>
                  );
                })}
              </div>
              {errors.userType && <p className="text-xs text-rose-600">{errors.userType}</p>}

              <TextField
                fullWidth
                label="Username"
                name="username"
                value={userData.username}
                onChange={changeHandler}
                error={!!errors.username}
                helperText={errors.username}
                inputProps={{ "aria-label": "username" }}
                size="small"
              />

              <TextField
                fullWidth
                label="Email"
                name="email"
                value={userData.email}
                onChange={changeHandler}
                error={!!errors.email}
                helperText={errors.email}
                inputProps={{ "aria-label": "email" }}
                size="small"
              />

              <TextField
                fullWidth
                label="Password"
                type={showPassword ? "text" : "password"}
                name="password"
                value={userData.password}
                onChange={changeHandler}
                error={!!errors.password}
                helperText={errors.password || "Minimum 8 characters"}
                size="small"
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label={showPassword ? "Hide password" : "Show password"}
                        onClick={handleClickShowPassword}
                        edge="end"
                        size="large"
                      >
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />

              <TextField
                fullWidth
                label="Confirm Password"
                type={showConfirmPassword ? "text" : "password"}
                name="confirmPassword"
                value={userData.confirmPassword}
                onChange={changeHandler}
                error={!!errors.confirmPassword}
                helperText={errors.confirmPassword}
                size="small"
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label={showConfirmPassword ? "Hide confirm password" : "Show confirm password"}
                        onClick={handleClickShowConfirmPassword}
                        edge="end"
                        size="large"
                      >
                        {showConfirmPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />

              <button
                type="submit"
                disabled={isSubmitting}
                className="w-full py-3 rounded-xl text-sm font-semibold text-white bg-gradient-to-r from-slate-800 to-teal-600 hover:from-slate-900 hover:to-teal-500 transition-opacity disabled:opacity-60 disabled:cursor-not-allowed"
                aria-busy={isSubmitting}
              >
                {isSubmitting ? "Registering..." : "Register"}
              </button>
            </form>

            <div className="mt-4 text-center">
              <p className="text-sm text-slate-600">Already have an account?</p>
              <Link href="/Login" className="mt-3 inline-block w-full py-2 bg-slate-800 text-white rounded-xl hover:bg-slate-900 transition">
                Go to Login
              </Link>
            </div>
          </div>
        </div>
      </main>

      <ToastContainer position="top-right" newestOnTop />
    </div>
  );
};

export default Signup;